<template>
  <div>
    <v-card tile style="width: 100%">

      <v-card-title>志愿填报预测</v-card-title>
      <v-card-subtitle>选择您的三个志愿</v-card-subtitle>
      <v-divider></v-divider>
      <v-row>
        <v-col cols="5">
          <v-combobox
              v-model="finalSelectionName"
              :items="getCollegeName(entireCollegeInformation)"
              style="margin-left: 2vh"
              label="选择你的志愿..."
              multiple
              chips
          >
          </v-combobox>
        </v-col>

        <v-row style="margin-left: 5vh">
          <v-row cols="24" sm="6" md="6">
            <v-btn
                class="align-center white--text"
                color="primary"
                width="15vh"
                height="4vh"
                style="alignment: center; margin-top: 2vh;margin-left: 2vh"
                @click="this.getAllCollegeInformation"
            >刷新大学列表
            </v-btn>


          </v-row>

          <v-row cols="24" sm="6" md="6">
            <v-btn class="d-flex align-center white--text"
                   color="blue"
                   width="15vh"
                   height="4vh"
                   style="margin-top: 2vh"
                   @click="functionChoose"
            >
              {{ label[buttonFlag] }}
            </v-btn>
          </v-row>

          <v-row cols="24" sm="6" md="6">
            <v-btn width="15vh" @click="getMyCollege" color="success" style="margin-top: 2vh">志愿</v-btn>
            </v-row>

        </v-row>
      </v-row>


      <template>
        <v-row justify="space-around">


        </v-row>
      </template>

      <v-form v-model="valid">
        <v-container>
          <v-row>
            <v-col cols="12" md="4">
              <v-combobox
                  v-model="finalSelectionName[0]"
                  :items="getCollegeName(entireCollegeInformation)"
                  label="志愿1"
                  required
                  chips
              ></v-combobox>
            </v-col>

            <v-col cols="12" md="4"
            >
              <v-combobox
                  v-model="finalSelectionName[1]"
                  :items="getCollegeName(entireCollegeInformation)"
                  label="志愿2"
                  required
                  chips
              ></v-combobox>
            </v-col>

            <v-col cols="12" md="4">
              <v-combobox
                  v-model="finalSelectionName[2]"
                  :items="getCollegeName(entireCollegeInformation)"
                  label="志愿3"
                  required
                  chips
              ></v-combobox>
            </v-col>
          </v-row>
        </v-container>
      </v-form>
    </v-card>
    <br>
    <v-row>

    </v-row>
    <br>


    <v-data-table
        :headers="headers"
        :items="entireCollegeInformation"
        item-key="name"
        class="elevation-1"
        :custom-filter="filterOnlyCapsText"
    >

    </v-data-table>
    <v-divider></v-divider>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      label: ["修改志愿", "增加志愿"],
      // 反正这个看起来有用, 先这么写着
      valid: '',

      // 存学号
      studentID: '',

      // 最后选择的东西, 要传给后端进行计算, 这是一部分
      finalSelectionName: [],

      // 所有信息
      entireCollegeInformation: [],

      // 0 -> 已经填了志愿
      // 1 -> 还没填写志愿
      buttonFlag: 1,

      //获取志愿录取信息
      getCollegeData:' ',
    }
  },

  // 底下那个大表格
  computed: {
    headers() {
      return [
        {
          text: '学校ID',
          // align: 'start',
          // sortable: false,
          value: 'collegeID',
        },
        {text: '学校名字', value: 'name'},
        {text: '学校分数', value: 'score'},
        {text: '学校批次', value: 'tier'},
        {text: '学校位次', value: 'crank'},
        {text: '学校种类', value: 'kind'},
        {text: '当前报考人数', value: 'countSelected'},
      ]
    },
  },

  mounted: function () {
    this.studentID = localStorage.getItem("userName");
    localStorage.setItem("collegeData"," ");
    //alert("学号为: " + this.studentID +"的同学, 你好!");
    this.getAllCollegeInformation();
    this.judgeIntention();
  },

  methods: {
    functionChoose: function () {
      if(this.finalSelectionName[0]!=null
          && this.finalSelectionName[1] != null
          && this.finalSelectionName[2] != null
          && this.finalSelectionName[0] != this.finalSelectionName[1]
          && this.finalSelectionName[0] != this.finalSelectionName[2]
          && this.finalSelectionName[1] != this.finalSelectionName[2]){
        if (this.buttonFlag === 0) {
          this.changeIntention();
        } else if (this.buttonFlag === 1) {
          this.addIntention();
        }
      } else{
        alert("志愿填写错误");
      }
    },

    // 这个是刚加载页面的时候运行的函数
    judgeIntention: function () {
      // alert("进入刷新页面函数");
      var that = this;
      var url = 'http://localhost:9090/wish_sheet/judge';

      axios({
        url: url,
        method: 'post',
        params: {
          id: that.studentID,
        },
      })
          .then(function (response) {
            // alert(response.data.code);
            if (response.data.code === "0") {
              // alert("这人已经填报过了");
              that.buttonFlag = 0;
              // alert("现在按钮的状态是: " + that.buttonFlag);
            } else if (response.data.code === "1") {
              // alert("这个逼还没填");
              that.buttonFlag = 1;
              // alert("现在按钮的状态是: " + that.buttonFlag);
            } else {
              alert("现在按钮的状态是: " + that.buttonFlag);
            }
          }).catch(function () {
        // alert("加载页面的时候报错了");
      })
    },

    // 这个是增加志愿的函数
    addIntention: function () {
      // alert("进入增加志愿函数");
      var url = "http://localhost:9090/wish_sheet/add";
      // alert("studentID是: " + this.studentID);


      axios({
        url: url,
        method: 'post',
        params: {
          id: this.studentID,
          wishID1: (this.entireCollegeInformation.find((item) => item.name === this.finalSelectionName[0]).collegeID),
          wishID2: (this.entireCollegeInformation.find((item) => item.name === this.finalSelectionName[1]).collegeID),
          wishID3: (this.entireCollegeInformation.find((item) => item.name === this.finalSelectionName[2]).collegeID),
        }
      })
          .then(function (response) {
            // alert("报的志愿传递给后端成功");
            if (response.data.code === "0") { // 成功
               alert("增加志愿成功");
            } else if (response.data.code === "1") { // 失败
               alert("增加志愿失败");
            } else { //不可能的情况
              // alert("不可能的情况")
            }
          }).catch(function () {
        alert("增加志愿报错");
      })
    },

    // 这个是修改志愿的函数
    changeIntention: function () {
      // alert("进入修改志愿函数");
      var url = "http://localhost:9090/wish_sheet/update";
      var id = this.studentID;

      axios({
        url: url,
        method: 'post',
        params: {
          id: id,
          wishID1: (this.entireCollegeInformation.find((item) => item.name === this.finalSelectionName[0]).collegeID),
          wishID2: (this.entireCollegeInformation.find((item) => item.name === this.finalSelectionName[1]).collegeID),
          wishID3: (this.entireCollegeInformation.find((item) => item.name === this.finalSelectionName[2]).collegeID),
        }
      })
          .then(function (response) {
            if (response.data.code === "0") { // 成功
              // alert("修改志愿成功");
            } else if (response.data.code === "1") { // 失败
              // alert("修改志愿失败");
            } else { //不可能的情况
              alert("修改志愿 -> 不可能的情况")
            }
          }).catch(function () {
        alert("修改志愿报错")
      })
    },

    // 向后端请求, 获得所有大学的信息
    getAllCollegeInformation: function () {
      var that = this;
      var url = 'http://localhost:9090/college/all';
      axios.defaults.headers.common['token'] = localStorage.getItem("token");

      axios({
        url: url,
        method: 'get',
        params: null,
      })
          .then(function (response) {
            //alert("已刷新所有大学的信息");
            that.entireCollegeInformation = response.data.data;
          })
          .catch(function () {
            alert("刷新所有大学的信息失败");
          })
    },

    // hello！
    getCollegeName(ObjList) {
      var list = [];
      for (var i = 0; i < ObjList.length; i++) {
        list.push(ObjList[i]['name'])
      }
      return list;
    },

    filterOnlyCapsText(value, search) {
      return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleUpperCase().indexOf(search) !== -1
    },
    getMyCollege: function () {
      var url = "http://localhost:9090/student/get-my-college";
      var that = this;


      axios({
        url: url,
        method: 'post',
        params: {
          studentID: that.studentID,
        },
      })
          .then(function (response) {
            that.getCollegeData = response.data.data;
            if(response.data.data === 'not_yet'){
              that.getCollegeData = "很抱歉，志愿尚未开始录取。"
            }else if(response.data.data === 'failed'){
              that.getCollegeData = "很抱歉，您未被院校录取。"
            }else{

              that.getCollegeData = "您已被录取，录取院校为："  + response.data.data
            }
            localStorage.setItem("collegeData", that.getCollegeData);
            alert(that.getCollegeData);
          })
          .catch(function () {
            alert("刷新所有大学的信息失败");
          })
    },
  },
}
</script>